.header {
  background-color: #282c34;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 20px;
}
.main-cont{
  padding: auto;
}
.lev1-cont{
  width: 80%;
  min-height: 50px;
  margin: auto;
  border: 1px solid black;
  border-radius: 5px;
}
.bgc{
  background-color: gray;
    border-bottom: 1px solid;
    padding-top: 5px;
    margin-bottom: 5px;
}
.lev1-items{
  padding-top: 19px;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight:bold;
  justify-content: space-between;
  margin-bottom: 20px;
}

.lev2-cont{
  width: 90%;
  min-height: 50px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid black;
  border-radius: 5px;
}
.lev3{
  padding: 10px;
  display: grid;
  gap: 20px;
  grid-template-columns: auto auto auto;
  font-weight: bold;
}
.icon{
  font-size: larger;
  font-weight: bolder;
  text-align: right;
}